  created() {
    // 获取code
    this.$nextTick(() => {
      let code = this.GetQueryString('code');
      // console.log("code", code)
      if (code) {
        this.code = code;
        localStorage.setItem('code',this.code)
      }else{
        let codeOld = window.localStorage.getItem('code');
        // console.log('codeOld',codeOld)
        if (codeOld) {
          this.code = codeOld;
        }
      }  
      this.getShareLink();
    });
