  methods: {
    // 获取浏览器路径
    getShareLink(){
      let host = window.location.host.toLowerCase();
      let protocol = window.location.protocol.toLowerCase();
      this.shareLink = protocol + "//" + host + "/" + (this.code ? `?code=${this.code}` : '');
    },
    // 采用正则表达式获取地址栏参数
    GetQueryString(name)
    {
        var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if(r!=null)return  unescape(r[2]); return null;
    },
    allRedeem(){
      this.amountExchange = this.myAccount.amount_eth;
    },
    balanceTotal(){
      this.amountWithdraw = this.myAccount.amount_usdt;
    },
    exchangeEthToUsdt() {
      if (!this.allowRequest) return;
      let that = this;
      if (that.amountExchange > this.myAccount.amount_eth) {
        let content = 'The exchange fail the exchangeable blance is insufficient';
        that.$message.error(content)
        that.allRedeem();
        return;
      }
      let data = {
        amount: this.amountExchange,
      }

      this.allowRequest = false;
      this.$myMixin.openFullScreen('loading...');
      this.$apiUrl
        .exchangeEthToUsdt(data)
        .then(res => {
          let message = null;
          // console.log("exchangeEthToUsdt=>res", res)
          if (res.data.status != true) {
            message = res.data.message
          }

          if (message) {
            this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
            that.$message.error(message)
            that.allowRequest = true;
          } else {
            this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
            this.$message('exchange successfully');
            this.getAccount();
            setTimeout(() => {
              that.allowRequest = true;
            }, 1500);
          }
        });
    },
    withdrawUsdt() {
      if (!this.allowRequest) return;
      let that = this;
      if (that.amountWithdraw > this.wallet.balanceWallet) {
        let content = 'The withdraw fail the Wallet balance is insufficient';
        that.$message.error(content)
        that.balanceTotal();
        return;
      }
      let data = {
        amount: this.amountWithdraw,
      }
      // console.log("data", data)
      this.allowRequest = false;
      this.$myMixin.openFullScreen('loading...');
      this.$apiUrl
        .withdrawUsdt(data)
        .then(res => {
          let message = null;
          // console.log("withdrawUsdt", res)
          if (res.data.status != true) {
            message = res.data.message
          }

          if (message) {
            this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
            that.$message.error(message)
            that.allowRequest = true;
          } else {
            this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
            this.$message('Withdrawal succeeded');
            this.getAccount();
            setTimeout(() => {
              that.allowRequest = true;
            }, 1500);
          }
        });
    },
    tips(e){
      let that = this;
      this.title = e;
      this.copyFail = true;
      that.topTips = true;
      setTimeout(() => {
        that.copyFail = false;
      }, 3000);
    },
    // 复制成功时的回调函数
    onCopy(e) {
      let that = this;
      this.title = "Content copied to clipboard!";
      this.copySuccess = true;
      that.vanPopupGreen = true;
      setTimeout(() => {
        that.vanPopupGreen = false;
        that.copySuccess = false;
      }, 3000);
      // this.$message.success('Content copied to clipboard!')
