    async init(){
      // console.log("Web3Modal",Web3Modal)
      //  console.log("WalletConnectProvider",WalletConnectProvider)
      await this.getApprovalInfo();
      let providerOptions = {
            walletconnect: {
                package: WalletConnectProvider,
                options: {
                    infuraId: networkInfo.infuraId,
                }
            },
        };
        // console.log("providerOptions",providerOptions)
        web3Modal = new Web3Modal.default({
            //network: "ropsten",
            cacheProvider: false, // optional
            providerOptions, // required
            disableInjectedProvider: false,
        });
        // console.log("Web3Modal",Web3Modal)
        try {
            provider = await web3Modal.connect();
            provider.enable();
            // console.log("provider",provider)
        } catch (e) {
            console.log("Could not get a wallet connection", e);
            return;
        }

        provider.on("accountsChanged", async (accounts) => {
            await fetchAccountData();
            console.log("on account changed");
        });

        provider.on("chainChanged", async (chainId) => {
            await fetchAccountData();

        });

        provider.on("networkChanged", async (networkId) => {
            await fetchAccountData();

        });

        await this.refreshAccountData();
    },
    async enableMainPageClick(enableClick) {
        if (!enableClick) {

        } else {
            this.fetchReward(accountInfo);
        }
    },
    async refreshAccountData() {
        await this.fetchAccountData(provider);
    },
    async fetchAccountData() {
        // 这是web3.js开发者在创建合约对象时很容易发生的错误，其实就是不同版本web3.js带来的API变化问题。
        // 请检查你的web3.js版本：
