        // 2、如果version>1.0.0，使用：
        // new web3.eth.Contract(studentFactoryArtifact,address); // 注意区分Contract大小写

        web3 = Web3;
        if (typeof web3 !== 'undefined') {
          web3 = new Web3(provider);
          // web3 = new Web3(Web3.currentProvider);
        } else {
          web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));
        }
        // console.log("web3",web3)
        if(this.myAccount){
          accountInfo.userAddress = this.myAccount.address || '';
        }
    },
    async fetchReward(acc) {
        /*
        if (accountInfo.userAddress===null || accountInfo.userAddress===''){
            popMessage('#pages', 'Please wait...connecting to your wallet.');
        }

