        width: 100%;
        padding: 0.26667rem 0.42667rem;
        overflow: hidden;
        color: #323233;
        font-size: 0.37333rem;
        line-height: 0.64rem;
      }

      .van-cell--clickable {
        cursor: pointer;
      }

      .van-cell__title {
        color: #081724;
        font-weight: 700;
        font-size: 0.42667rem;
        text-align: left;
      }

      .van-cell__right-icon {
        margin-left: 0.10667rem;
        color: #969799;
      }

      .van-cell__left-icon,
      .van-cell__right-icon {
        height: 0.64rem;
        font-size: 0.42667rem;
        line-height: 0.64rem;
      }

      .van-collapse-item__wrapper {
        overflow: hidden;
        transition: height 0.3s ease-in-out;
        will-change: height;
      }

      .van-collapse-item__content {
        background: transparent;
        text-align: left;
        padding: 0.32rem 0.42667rem;
        color: #969799;
        font-size: 0.37333rem;
        line-height: 1.5;
      }
    }
  }

  .programme-list{
    padding: 0.26667rem 0.13333rem 0.40667rem;
    margin: 0.2rem 0.4rem;

    .programme-item{
      display: flex;
      justify-content: space-between;
      align-items: center;

      .programme-title{
        width: 2.5rem;
        text-align: left;
      }
    }

    .programme-item-btm{
      height: 80px;
      position: relative;
      border: 0.02667rem solid #cccccc;
      padding: 0  0.13667rem;
      border-radius: 0.26667rem;
      
      overflow-y: auto;
      margin-top: .4rem;
      width: 75%;
      box-sizing: border-box;

      // width: 100%;
      // display: flex;
      // flex-direction: row;
      // align-items: center;
      // padding: 0.26667rem 0.13333rem;
      // min-height: 80px;
      
      .programme-remark{
        // overflow-y: auto;
        // text-align: left;
      }
    }
  }

  // 自定义下拉框样式start
  .divInput{
    position: relative;

    ul li{
      list-style: none;
    }
    .input{
      width: 140px;
      height: 40px;
      line-height: 40px;
      padding-left: 10px;
      border: 1px solid #cccccc;
      position: relative;
      border-radius: 4px;
    }
    .input input{
      border: none;
      outline: none;
      width: 90%;
    }
    .input img{
      position: absolute;
      // right: 34px;
      // top: 48%;

      right: 15px;
      top: 10px;
      width: 20px;
      height: auto;
    }
    .list{
      width: 150px;
      border: 1px solid #cccccc;
      overflow: hidden;
      border-radius: 4px;
      position: absolute;
      background: #ffffff;
      z-index: 10;
    }
    .list ul li{
      width: 100%;
      height: 30px;
      cursor: pointer;
      line-height: 30px;
      // padding-left: 10px;
    }
    .list ul li:hover{
      background-color: #cccccc;
    }
  }
  // 自定义下拉框样式end

  // 自定义金额
  .divInput1{
    width: 75%;

    .input{
      width: 100%;
      margin-top: .4rem;
      padding-left: 0px;
    }
  }

  .defi-exchange {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 0.02667rem solid #ebedf3;
    border-radius: 0.26667rem;
    margin: 0.4rem;
    padding: 0.26667rem 0.13333rem;

    .defi-exchange-field {
      width: 3.46667rem;
      font-family: DBOzoneX-Medium;
      font-size: 0.8rem;
      background: transparent;
    }

    .van-cell__value {
      position: relative;
      vertical-align: middle;
      word-wrap: break-word;
    }

    // .van-field__body {
    //   display: flex;
    //   align-items: center;

    //   .van-field__control {
    //     display: block;
    //     box-sizing: border-box;
    //     width: 100%;
    //     min-width: 0;
    //     margin: 0;
    //     padding: 0;
    //     color: #323233;
    //     line-height: inherit;
    //     text-align: left;
    //     background-color: transparent;
    //     border: 0;
    //     resize: none;
    //     outline: none;
    //     font-size: 0.6rem;
    //   }
    // }

    .van-cell:after {
      position: absolute;
      box-sizing: border-box;
      content: " ";
      pointer-events: none;
      right: 0.42667rem;
      bottom: 0;
      left: 0.42667rem;
      border-bottom: 0.02667rem solid #ebedf0;
      transform: scaleY(0.5);
    }

    .van-icon-sort {
      font-size: 0.53333rem;
      width: 0.53333rem;
      height: 0.53333rem;
    }

    .van-icon {
      position: relative;
      font: normal normal normal 0.37333rem/1 vant-icon;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
    }

    .van-icon-sort:before {
      content: "\F0EA";
    }

    .van-icon,
    .van-icon:before {
      display: inline-block;
    }

    .van-icon,
    .van-icon:before {
      display: inline-block;
    }

    .defi-exchange-to {
      width: 3.46667rem;
      text-align: center;
    }

    .icon-USDT {
      font-size: 0.58667rem;
      margin-left: 0.53333rem;
    }

    .defi-exchange-usdt {
      font-size: 0.42667rem;
      margin-left: 0.26667rem;
    }
  }

  .van-cell__value--alone {
    color: #323233;
    text-align: left;
  }

  .van-field__value {
    overflow: visible;
  }

  .defi-exchange-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 0.4rem;
  }

  .defi-exchange-submit {
    border-radius: 0.13333rem;
    width: 4.93333rem;
  }

  .van-button--primary {
    color: #fff;
    background-color: #07c160;
    border: 0.02667rem solid #07c160;
  }

  .defi-exchange-tip {
    border-top: 0.02667rem solid #ebedf3;
    margin-left: 0.4rem;
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    color: #a1a1b3;
  }

  .van-tabs--card {
    .van-tabs--card > .van-tabs__wrap {
      height: 0.8rem;
    }

    .van-tabs__nav--card {
      box-sizing: border-box;
      height: 0.8rem;
      margin: 0 0.42667rem;
      // border: .02667rem solid #ee0a24;
      border: 0.02667rem solid #ff8517;
      border-radius: 0.05333rem;

      .van-tab {
        color: #ee0a24;
        border-right: 0.02667rem solid #ee0a24;
        .van-tab__text--ellipsis {
          color: #ee0a24;
          font-weight: 500;
        }
      }

      .van-tab:last-child {
        border-right: none;
      }

      .van-tab.van-tab--active {
        .van-tab__text--ellipsis {
          color: #fff;
        }
        background: #ff8517;
      }
    }

    .van-row {
      border-bottom: 0.02667rem solid #ebedf0;
      padding-top: 0.13333rem;
      padding-bottom: 0.13333rem;
      display: flex;
      justify-items: center;
      align-items: center;
    }

    .record-list{
      height: 7.2rem;
      overflow: auto;
      
      .van-row{
        padding-top: 0.23333rem;
      }
    }

    .van-tabs__nav {
      position: relative;
      display: flex;
      background-color: #fff;
      user-select: none;
    }
  }
}

.van-row:after {
  display: table;
  clear: both;
  content: "";
}

.van-field__body {
  display: flex;
  align-items: center;

  .van-field__control {
    display: block;
    box-sizing: border-box;
    width: 100%;
    min-width: 0;
    margin: 0;
    padding: 0;
    color: #323233;
    line-height: inherit;
    text-align: left;
    background-color: transparent;
    border: 0;
    resize: none;
    outline: none;
    font-size: 0.6rem;
  }
}

.van-button {
  border-radius: 0.13333rem;
}

button,
input,
select,
textarea {
  outline: none;
}

.warp ul a,
.warp ul li {
  display: block;
  height: 0.8rem;
  line-height: 0.8rem;
  display: flex;
  justify-content: space-between;
  font-size: 0.4rem;
  position: relative;

  .address {
    color: #2082fe;
  }

  .quantity {
    color: #060606;
    font-weight: 700;
  }
}

// 分享弹框
.van-dialog {
  position: fixed;
  overflow: hidden;
  font-size: 0.42667rem;
  backface-visibility: hidden;
  transition: 0.3s;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transform: translateZ(0);
  border-radius: 0;
  background: #fbfbfb;

  h1 {
    padding-left: 0.4rem;
    font-size: 0.53333rem;
  }

  .van-icon-cross {
    padding-top: 0.4rem;
    width: 0.5rem;
    height: 0.5rem;
    margin-right: 0.26667rem;
  }

  .van-icon-cross:before {
    line-height: 1.44rem;
    margin-right: 0.26667rem;
  }
  .van-icon-cross:before {
    content: "\F042";
  }
  .van-icon,
  .van-icon:before {
    display: inline-block;
  }

  .defi-share {
    margin: 0.4rem;
    background: #fff;
    padding: 0.4rem;
    border-radius: 0.4rem;
    color: #a1a1b3;

    .van-cell__value {
      width: 100%;
    }

    button,
    input,
    select,
    textarea {
      outline: none;
    }
  }

  h3 {
    color: #a1a1b3;
    text-align: left;
  }

  .van-field__control {
    display: block;
    box-sizing: border-box;
    width: 100%;
    min-width: 0;
    margin: 0;
    padding: 0;
    color: #323233;
    line-height: inherit;
    text-align: left;
    background-color: transparent;
    border: 0;
    resize: none;
  }

  .van-field__control_share{
    margin-right: 0.32rem;
  }

  van-field__button {
    padding-left: 0.21333rem;
  }
  .van-field__button,
  .van-field__clear,
  .van-field__icon,
  .van-field__right-icon {
    -webkit-flex-shrink: 0;
    flex-shrink: 0;
  }

  .van-button {
    border-radius: 0.13333rem;
  }

  .van-button--small {
    height: 0.85333rem;
    padding: 0 0.21333rem;
    font-size: 0.32rem;
  }

  .van-field__control {
    font-size: 0.42667rem;
  }

  .van-cell {
    position: relative;
    display: flex;
    box-sizing: border-box;
    width: 100%;
    padding: 0.26667rem 0;
    overflow: hidden;
    color: #323233;
    font-size: 0.37333rem;
    line-height: 0.64rem;
    background-color: #fff;
  }

  .van-button {
    border: none;
  }
}

.van-cell:focus {
  background: inherit !important;
}

// 取消点击背景颜色
.van-cell--clickable,
.van-tab,
.defi-share,
.van-icon-cross,
.van-nav-bar__right,
.van-nav-bar__left {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;
}

//顶部弹框提示
.van-notify {
  display: flex;
  align-items: center;
  justify-content: center;
  // box-sizing: border-box;
  padding: 0.21333rem 0.42667rem;
  color: #fff;
  font-size: 0.37333rem;
  line-height: 0.53333rem;
  white-space: pre-wrap;
  text-align: center;
  word-wrap: break-word;

  height: 0.53333rem;
  box-sizing: content-box;
}
.van-notify--warning {
  background-color: #ff976a;
}
.van-popup--top {
  // top: 0;
  left: 0;
  width: 100%;
}
.van-popup {
  position: fixed;
  max-height: 100%;
  overflow-y: auto;
  // transition: -webkit-transform .3s;
  // transition: transform .3s;
  // transition: transform .3s,-webkit-transform .3s;
  transition: all 0.3s ease-in-out; //这是滑入划出的动画
}

.van-popup-green {
  background: #07c160;
}

// 中部弹框
.van-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.van-dialog-middle {
  position: fixed;
  top: 45%;
  left: 50%;
  bottom: inherit;
  width: 8.53333rem;
  height: auto;
  overflow: hidden;
  font-size: 0.42667rem;
  background-color: #fff;
  border-radius: 0.42667rem;
  transform: translate3d(-50%, -50%, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: 0.3s;

  .van-dialog__header {
    font-weight: 700;
    font-size: 0.42667rem;
  }

  .van-dialog__header {
    padding-top: 0.69333rem;
    font-weight: 700;
    line-height: 0.64rem;
    text-align: center;
  }

  .van-button--danger {
    color: #fff;
    background-color: rgb(255, 133, 23);
    border: 0.02667rem solid rgb(255, 133, 23);
  }
}

/* $message弹框样式 */
.el-message{
  /* top: 50% !important;
  margin-top: -20px; */
  // top: 0px !important;
}

// more
.more{
  color: #999;
      text-align: center;
      cursor: pointer;
}

</style>



